import React, { Component } from 'react';
import SchoolIcon from 'material-ui-icons/School';
import WorkIcon from 'material-ui-icons/Work';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

const ResumeEduAndWork =(props)=> {
  console.log( props);
  console.log( props.educations);
  console.log( props.experiences);
  const VerticalTimelineEduAndWork = (
      <VerticalTimeline>
      {props.experiences.map((experience,key) =>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date={experience.date}
          iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
          icon={<WorkIcon />}
          key={key}
        >
          <h3 className="vertical-timeline-element-title">{experience.jobTitle}</h3>
          <h4 className="vertical-timeline-element-subtitle">{experience.company}</h4>
          {experience.jobDescription.map((description,key) =>
            <p>{description.description}</p>
            )}
            
        </VerticalTimelineElement>
      )}
      {props.educations.map((education,key) =>
        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          date={education.date}
          iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
          icon={<SchoolIcon />}
          key={key}
        >
          <h3 className="vertical-timeline-element-title">{education.degree}</h3>
          <h4 className="vertical-timeline-element-subtitle">{education.institution}</h4>
          <p>
            {education.description}
          </p>
        </VerticalTimelineElement>
      )}
      </VerticalTimeline>
  );
  /*experiences.map((experience) =>
      console.log( experience)
      )
  educations.map((education) =>
      console.log( education)
      )*/
  
	return (

	<div className="ResumeEduAndWork" style={{ background: 'orange' }}>
		I'm EduWork.
    {VerticalTimelineEduAndWork}
	</div>
	)
};


export default ResumeEduAndWork;



